.material-icons{
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-feature-settings: 'liga';
}

.card{
    width: 24vw;
    height: 38vw;
    border-radius: 0 !important;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px !important;
    background-color: #ffffff !important;
}

.activator{
    border-radius: 0 !important;
}

.card-content{
    padding: 1.25vw 1.5vw !important;
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 1.25vw;
}

#name-card-content{
    font-size: 2vw;
    font-weight: 900;
    font-family: 'Urbanist';
    color: #000000;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 3.75vw;
}

#more-vert{
    font-size: 2vw;
    color: #000000;
}

.me-job{
    font-size: 1.25vw;
    font-weight: 500;
    font-family: 'Urbanist';
    color: #000000;
}

.location{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 1vw;
    font-size: 1.25vw;
    font-weight: 500;
    font-family: 'Urbanist';
    color: #000000;
}

.contact{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 1vw;
    color: #000000;
}

.instagram ,
.telegram ,
.twitter ,
.whatsapp{
    color: #000000;
    font-size: 2.25vw;
}

.telegram-btn{
    position: relative;
    left: -0.5vw;
}

.whatsapp-btn{
    position: relative;
    left: -0.5vw;
}

.card-reveal{
    overflow: hidden !important;
}

#header-reveal{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 3.5vw;
}

.neme-card-reveal{
    font-size: 2vw;
    font-weight: 900;
    font-family: 'Urbanist';
    color: #000000;
}

#close{
    color: #000000;
    font-size: 2vw;
    font-weight: 500;
    font-family: 'Urbanist';
    z-index: -1;
}

.description-card-reveal{
    color: #000000;
    font-size: 1vw;
    font-weight: 500;
    font-family: 'Urbanist';
    display: flex;
    flex-direction: column;
    gap: 1vw;
}

.phone-number{
    font-family: 'Gill Sans';
    font-size: 1vw;
    font-weight: 500;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 1vw;
    text-decoration: none;
    color: #000000;
}

.phone{
    font-family: 'Urbanist';
    font-weight: 500;
    color: #000000;
}

.skills-container{
    display: flex;
    flex-direction: column;
    font-family: 'Urbanist';
    gap: 1vw;
}

.tittle-skills{
    font-size: 1.25vw;
    font-weight: 800;
    color: #000000;
}

.skills{
    font-size: 1vw;
    font-weight: 500;
    color: #000000;
    display: flex;
    flex-direction: row;
    gap: 3vw;
}

.item{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.25vw;
}

.point{
    font-size: 0.75vw;
}

@media only screen and (max-width:426px){
    
    .card{
        width: 80vw;
        height: 125vw;
        border-radius: 0 !important;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px !important;
        background-color: #ffffff !important;
    }
    
    .activator{
        border-radius: 0 !important;
    }
    
    .card-content{
        padding: 4.25vw 4.5vw !important;
        display: flex;
        flex-direction: column;
        justify-content: start;
        gap: 3vw;
    }
    
    #name-card-content{
        font-size: 8vw;
        font-weight: 900;
        font-family: 'Urbanist';
        color: #000000;
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        gap: 3vw;
    }
    
    #more-vert{
        font-size: 7vw;
        color: #000000;
    }
    
    .me-job{
        font-size: 4vw;
        font-weight: 500;
        font-family: 'Urbanist';
        color: #000000;
    }
    
    .location{
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        gap: 1vw;
        font-size: 4vw;
        font-weight: 500;
        font-family: 'Urbanist';
        color: #000000;
    }
    
    .contact{
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        gap: 4vw;
        color: #000000;
    }
    
    .instagram ,
    .telegram ,
    .twitter ,
    .whatsapp{
        color: #000000;
        font-size: 8vw;
    }
    
    .telegram-btn{
        position: relative;
        left: -0.5vw;
    }
    
    .whatsapp-btn{
        position: relative;
        left: -0.5vw;
    }
    
    .card-reveal{
        overflow: hidden !important;
    }
    
    #header-reveal{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    
    .neme-card-reveal{
        font-size: 7vw;
        font-weight: 900;
        font-family: 'Urbanist';
        color: #000000;
    }
    
    #close{
        color: #000000;
        font-size: 7vw;
        font-weight: 500;
        font-family: 'Urbanist';
        z-index: -1;
        float: right;
    }
    
    .description-card-reveal{
        color: #000000;
        font-size: 3.5vw;
        font-weight: 500;
        font-family: 'Urbanist';
        display: flex;
        flex-direction: column;
        gap: 2vw;
    }
    
    .phone-number{
        font-family: 'Gill Sans';
        font-size: 3.5vw;
        font-weight: 500;
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        gap: 2vw;
        text-decoration: none;
        color: #000000;
    }
    
    .phone{
        font-family: 'Urbanist';
        font-weight: 500;
        color: #000000;
    }
    
    .skills-container{
        display: flex;
        flex-direction: column;
        font-family: 'Urbanist';
        gap: 2vw;
    }
    
    .tittle-skills{
        font-size: 4.5vw;
        font-weight: 800;
        color: #000000;
    }
    
    .skills{
        font-size: 3.5vw;
        font-weight: 500;
        color: #000000;
        display: flex;
        flex-direction: row;
        gap: 8vw;
    }
    
    .item{
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 0.5vw;
    }
    
    .point{
        font-size: 2.5vw;
    }
}

@media only screen and (min-width:426px) and (max-width:2560px){
    body{
        display: none;
    }
}
